<template lang="html">
  <div class="simulator">
    <h1>Network Test</h1>
    <div id="precall" ref="precall" class="prettyBox" v-if="hideStartTest">
      <!-- <button @click="startTest">Start test</button> -->
      <v-btn
        class="btn--33"
        depressed
        small
        color="#375197"
        style="color:#fff;"
        @click="startTest"
      >
        Start Test
      </v-btn>
    </div>

    <div id="connectivity_status_container" class="prettyBox" v-if="showConnectivityStatus">
      <div class="quality_header">
        <CheckCompabilitiesIcon :width=IC.ICwidth :height=IC.ICheight />
        <span>Checking Browser Compabilities</span>
        <CheckIcon
          width="32px"
          height="32px"
          color="#90E2DA"
          v-show="handleConnectivityRes"
        />
        <div class="loader" v-show="!handleConnectivityRes">
          <div class="loading-spinning-bubbles">
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="quality_header">
        <TestingVideoIcon :width=IC.ICwidth :height=IC.ICheight />
        <span>Testing Streaming Quality</span>
        <CheckIcon
          width="32px"
          height="32px"
          color="#90E2DA"
          v-show="handleQualityRes"
        />
        <div class="loader" v-show="!handleQualityRes">
          <div class="loading-spinning-bubbles">
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
            <div class="bubble-container">
              <div class="bubble"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="quality_status_container" class="prettyBox" v-show="handleConnectivityRes">
      <div class="simulator__bottom">
        <div class="simulator__graph" id="audio">
          <h3>Audio</h3>
          <div>
            <div class="" v-if="!handleQualityRes">
              <p>Please wait for result..</p>
            </div>
            <div class="results" v-if="handleQualityRes">
              <p>Supported:
                <span id="audio-supported">{{this.qualityRes.audio.supported}}</span>
              </p>
              <p>Quality:
                <span id="audio-mos">{{(this.qualityRes.audio.mos).toFixed(2)}} ({{this.rateMosScoreAudio}})</span>
              </p>
              <p>Bitrate:
                <span id="audio-bitrate">{{(this.qualityRes.audio.bitrate / 1000).toFixed(1)}}</span>
              </p>
              <p>Packet loss ratio:
                <span id="audio-plr">{{(this.qualityRes.audio.packetLossRatio).toFixed(2)}}</span>
              </p>
            </div>
            <div id="audioGraph" class="graph"></div>
          </div>
        </div>
        <div class="simulator__graph" id="video">
          <h3>Video</h3>
          <div>
            <div class="" v-if="!handleQualityRes">
              <p>Please wait for result..</p>
            </div>
            <div class="results" v-if="handleQualityRes">
              <p>Supported:
                <span id="video-supported">{{this.qualityRes.video.supported}}</span>
              <p id="video-unsupported-reason" v-if="handleReason">Reason:
                <span>{{this.qualityRes.video.reason}}</span>
              </p>
              <p>Quality:
                <span id="video-mos">{{(this.qualityRes.video.mos).toFixed(2)}} ({{this.rateMosScoreVideo}})</span>
              </p>
              <p>Bitrate:
                <span id="video-bitrate">{{(this.qualityRes.video.bitrate / 1000).toFixed(1)}}</span>
              </p>
              <p>Packet loss ratio:
                <span id="video-plr">{{(this.qualityRes.video.packetLossRatio).toFixed(2)}}</span>
              </p>
              <!-- <p>Recommended resolution:
                <span id="video-recommendedResolution">{{this.qualityRes.video.recommendedResolution}} @{{this.qualityRes.video.recommendedFrameRate}} fps</span>
              </p> -->
              <!-- <p>Recommended frame rate:
                <span id="video-recommendedFrameRate">{{this.qualityRes.video.recommendedFrameRate}} fps</span>
              </p> -->
            </div>
            <div id="videoGraph" class="graph"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OT from '@opentok/client';
import NetworkTest, { ErrorNames } from 'opentok-network-test-js';
import createChart from './js/chart.js';
import * as ConnectivityUI from './js/connectivity-ui.js';

import CheckCompabilitiesIcon from '../../components/icons/CheckCompabilitiesIcon.vue'
import TestingVideoIcon from '../../components/icons/TestingVideoIcon.vue'
import CheckIcon from '../../components/icons/CheckIcon.vue'

export default {
  components: {
    CheckCompabilitiesIcon,
    TestingVideoIcon,
    CheckIcon
  },
  data () {
    return {
      // session: {
      //   apiKey: '45489992',
      //   sessionId: '1_MX40NTQ4OTk5Mn5-MTU1NzExMzMwODg3M35yTnpMOVpaYS9XbjFjOEM4eG1BRFZyMTB-fg',
      //   token: '',
      //
      //   h264: {
      //     apiKey: '45992642',
      //     sessionId: '2_MX40NTk5MjY0Mn5-MTU1NzEyMjg2NzE1NX43MVJZOEdwTWJkNXFoV2NUL0RqZUpwTlB-fg',
      //     token: '',
      //   }
      // },
      // sessionInfo: {
      //   apiKey: '45489992',
      //   sessionId: '2_MX40NTQ4OTk5Mn5-MTU1NzIwMDExMDI4MX4vcDFmcUpQYm5pYm54M0VIOWxpM2l6MmJ-fg',
      //   token: '',
      // },
      otNetworkTest: null,
      hideStartTest: true,
      showConnectivityStatus: false,
      connectivityRes: null,
      qualityRes: null,
      IC: {
        ICwidth: '60px',
        ICheight: '100%'
      }
    }
  },
  created () {

  },
  mounted () {
    // const isSafari = /constructor/i.test(window.HTMLElement) || ((p) => { return p.toString() === "[object SafariRemoteNotification]"; })(!window['safari'] || (typeof safari !== 'undefined' && safari.pushNotification));
    //
    // if (isSafari && session.h264.apiKey) {
    //   sessionInfo = session.h264;
    // }

    const precallDiv = this.$refs.precall;
    const timeout = 15000;
    const options = {
      timeout: timeout
    };
    const dataSimulator = {
      apiKey: this.$store.state.opentokApiKey,
      sessionId: this.$store.state.stateSessionSimulator.simulator.session,
      token: this.$store.state.stateSessionSimulator.simulator.token,
    }
    this.otNetworkTest = new NetworkTest(OT, dataSimulator, options);
  },
  computed: {
    qualityResult () {
      if (this.qualityRes == null) {
        return 0
      } else {
        return this.qualityRes
      }
    },
    handleConnectivityRes () {
      if (this.connectivityRes == null) {
        return false
      } else {
        return true
      }
    },
    handleQualityRes () {
      if (this.qualityRes == null) {
        return false
      } else {
        return true
      }
    },
    handleReason () {
      if (this.qualityRes == null) {
        return false
      } else if (this.qualityRes.video.supported == true) {
        return false
      } else {
        return true
      }
    },
    rateMosScoreAudio () {
      if (this.qualityRes.audio.mos >= 3.8) {
        return 'Excellent';
      }
      if (this.qualityRes.audio.mos >= 3.1) {
        return 'Good';
      }
      if (this.qualityRes.audio.mos >= 2.4) {
        return 'Fair';
      }
      if (this.qualityRes.audio.mos >= 1.7) {
        return 'Poor';
      }
      return 'Bad';
    },
    rateMosScoreVideo () {
      if (this.qualityRes.video.mos >= 3.8) {
        return 'Excellent';
      }
      if (this.qualityRes.video.mos >= 3.1) {
        return 'Good';
      }
      if (this.qualityRes.video.mos >= 2.4) {
        return 'Fair';
      }
      if (this.qualityRes.video.mos >= 1.7) {
        return 'Poor';
      }
      return 'Bad';
    }
  },
  methods: {
    startTest () {
      this.hideStartTest = false
      this.showConnectivityStatus = true
      this.otNetworkTest.testConnectivity().then((results) => {
        console.log('OpenTok connectivity test results', results);
        this.connectivityRes = results
        createChart('audio');
        createChart('video');
        this.otNetworkTest.testQuality(function updateCallback(stats) {
          ConnectivityUI.checkToDisplayStopButton();
          ConnectivityUI.graphIntermediateStats('audio', stats);
          ConnectivityUI.graphIntermediateStats('video', stats);
        })
        .then((results) => {
          // This function is called when the quality test is completed.
          console.log('OpenTok quality results', results);
          this.qualityRes = results
          let publisherSettings = {};
          if (results.video.reason) {
            console.log('Video not supported:', results.video.reason);
            publisherSettings.videoSource = null; // audio-only
          } else {
            publisherSettings.frameRate = results.video.recommendedFrameRate;
            publisherSettings.resolution = results.video.recommendedResolution;
          }
          if (!results.audio.supported) {
            console.log('Audio not supported:', results.audio.reason);
            publisherSettings.audioSource = null;
            // video-only, but you probably don't want this -- notify the user?
          }
          if (!publisherSettings.videoSource && !publisherSettings.audioSource) {
            // Do not publish. Notify the user.
          } else {
            // Publish to the "real" session, using the publisherSettings object.
          }
        }).catch((error) => {
          console.log('OpenTok quality test error', error);
        });
      }).catch(function(error) {
        console.log('OpenTok connectivity test error', error);
      });
    },
  }
}
</script>

<style lang="scss">
  .simulator {
    display: flex;
    justify-content: center;
    flex-direction: column;

    #connectivity_status_container {
      width: 40%;
      margin: 30px auto 40px;
      display: flex;
      justify-content: space-around;
    }
    .quality_header {
      width: 32%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      & span {
        width: 80%;
        color: $black;
        font-size: .8em;
        text-align: center;
      }
    }
    & h1 {
      color: $black;
      font-weight: 600;
      text-align: center;
    }
    & #precall {
      display: flex;
      justify-content: center;

      @media screen and (max-width: 599px) {
        margin: 50px 0 0;
      }
    }
    &__bottom {
      display: flex;
      justify-content: space-between;
    }
    &__graph {
      width: 49%;
      background-color: $almostwhite;

      & h3 {
        color: $darkgrey;
        padding: 0 10px;
        font-weight: 600;
      }
      p {
        color: $black;
        margin: 5px 0;
        padding: 0 10px;
        font-weight: 400;
      }
    }
  }

  .loading-bubbles {
    margin: auto;
  }
  .loading-bubbles .bubble-container {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0 5px;
  }
  .loading-bubbles .bubble-container .bubble {
    border-radius: 50%;
    background-color: $skyblue;
    width: 100%;
    height: 100%;
    transform-origin: 50% 50%;
    animation: bubble 1.2s -0.6s infinite ease-out;
  }
  .loading-bubbles .bubble-container:nth-of-type(2n) .bubble {
    animation-delay: -0.3s;
  }
  .loading-bubbles .bubble-container:nth-of-type(3n) .bubble {
    animation-delay: 0s;
  }

  .loading-spinning-bubbles {
    position: relative;
    margin: 15px auto 0;
  }
  .loading-spinning-bubbles .bubble-container {
    position: absolute;
    top: calc(50% - 10px/2);
    left: calc(50% - 10px/2);
    transform-origin: -100% 50%;
  }
  .loading-spinning-bubbles .bubble-container .bubble {
    background: $lightblue;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    animation: bubble 1s infinite;
    animation-delay: inherit;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+1) {
    transform: translateX(200%) rotate(-90deg);
    animation-delay: -1.5s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+2) {
    transform: translateX(200%) rotate(-45deg);
    animation-delay: -1.375s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+3) {
    transform: translateX(200%);
    animation-delay: -1.25s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+4) {
    transform: translateX(200%) rotate(45deg);
    animation-delay: -1.125s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+5) {
    transform: translateX(200%) rotate(90deg);
    animation-delay: -1s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+6) {
    transform: translateX(200%) rotate(135deg);
    animation-delay: -0.875s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+7) {
    transform: translateX(200%) rotate(180deg);
    animation-delay: -0.75s;
  }
  .loading-spinning-bubbles .bubble-container:nth-of-type(0n+8) {
    transform: translateX(200%) rotate(225deg);
    animation-delay: -0.625s;
  }
  @keyframes bubble {
    0%, 100% {
      transform: scale(0);
    }
    50% {
      transform: scale(1);
    }
  }
  ol, ul {
    padding: 0;
  }
</style>



